import { useState, useEffect } from "react";
import { useSnackbar } from "notistack";
import { useNavigate, Link as RouterLink } from "react-router-dom";
// @mui
import {
  Box,
  Card,
  Table,
  Button,
  Switch,
  Tooltip,
  TableBody,
  Container,
  IconButton,
  TableContainer,
  TablePagination,
  FormControlLabel,
} from "@mui/material";
// redux
import { useDispatch, useSelector } from "../../redux/store";
import {
  getCustomers,
  deleteCustomer,
  deleteCustomerSuccess,
  deleteCustomers,
  deleteCustomersSuccess,
} from "../../redux/slices/customer";
// routes
import { PATH_DASHBOARD } from "../../routes/paths";
// hooks
import useSettings from "../../hooks/useSettings";
import useTable, { getComparator, emptyRows } from "../../hooks/useTable";
// components
import Page from "../../components/Page";
import Iconify from "../../components/Iconify";
import Scrollbar from "../../components/Scrollbar";
import HeaderBreadcrumbs from "../../components/HeaderBreadcrumbs";
import {
  TableNoData,
  TableSkeleton,
  TableEmptyRows,
  TableHeadCustom,
  TableSelectedActions,
} from "../../components/table";
// sections
import {
  CustomerTableRow,
  CustomerTableToolbar,
} from "../../sections/@dashboard/customer/customer-list";
import extractErrorMessage from "../../utils/error";
import { useTranslation } from "react-i18next";

// ----------------------------------------------------------------------

export default function CustomerListList() {
  const {
    dense,
    page,
    order,
    orderBy,
    rowsPerPage,
    //
    selected,
    setSelected,
    onSelectRow,
    onSelectAllRows,
    //
    onSort,
    onChangeDense,
    onChangePage,
    onChangeRowsPerPage,
  } = useTable({
    defaultOrderBy: "last_updated",
  });

  const { themeStretch } = useSettings();

  const navigate = useNavigate();

  const { t } = useTranslation();

  const TABLE_HEAD = [
    { id: "full_name", label: t("field.full_name"), align: "left" },
    { id: "ref_code", label: t("field.ref_code"), align: "center" },
    { id: "phone", label: t("field.phone"), align: "center" },
    { id: "country_name", label: t("field.country"), align: "center" },
    { id: "city", label: t("field.city"), align: "center" },
    { id: "address", label: t("field.address"), align: "center" },
    { id: "is_block", label: t("field.block"), align: "center" },
    { id: "", align: "right" },
  ];

  const dispatch = useDispatch();

  const { enqueueSnackbar } = useSnackbar();

  const { customers, isLoading } = useSelector((state) => state.customer);

  const [tableData, setTableData] = useState([]);

  const [filterName, setFilterName] = useState("");

  const handleRefresh = () => {
    dispatch(getCustomers(page + 1, rowsPerPage));
  };

  useEffect(() => {
    if (customers?.count < 1) {
      handleRefresh();
    }
  }, [dispatch, page, rowsPerPage]);

  useEffect(() => {
    if (customers?.count > 0) {
      setTableData(customers?.data);
    }
  }, [customers]);

  const handleFilterName = (filterName) => {
    setFilterName(filterName);
  };

  const handleDeleteRow = async (id) => {
    try {
      const result = await deleteCustomer(id);
      enqueueSnackbar(t(`success.${result}`));
      dispatch(deleteCustomerSuccess(id));
    } catch (error) {
      console.error(error);

      const errorMessage = extractErrorMessage(error);

      enqueueSnackbar(t(`error.${errorMessage}`), {
        variant: "error",
      });
    }
  };

  const handleDeleteRows = async (selected) => {
    try {
      const result = await deleteCustomers(selected);
      enqueueSnackbar(t(`success.${result}`));
      dispatch(deleteCustomersSuccess(selected));
    } catch (error) {
      console.error(error);

      const errorMessage = extractErrorMessage(error);

      enqueueSnackbar(t(`error.${errorMessage}`), {
        variant: "error",
      });
    }
    setSelected([]);
  };

  const handleEditRow = (id) => {
    navigate(PATH_DASHBOARD.customer.edit(Number(id)));
  };

  const dataFiltered = applySortFilter({
    tableData,
    comparator: getComparator(order, orderBy),
    filterName,
  });

  const denseHeight = dense ? 60 : 80;

  const isNotFound =
    (!dataFiltered.length && !!filterName) ||
    (!isLoading && !dataFiltered.length);

  return (
    <Page title={`Customer: ${t("p_customer.customer_list")}`}>
      <Container maxWidth={themeStretch ? false : "lg"}>
        <HeaderBreadcrumbs
          heading={t("p_customer.customer_list")}
          links={[
            { name: t("params.dashboard"), href: PATH_DASHBOARD.root },
            {
              name: t("sideBar.customer"),
              href: PATH_DASHBOARD.customer.root,
            },
            { name: t("p_customer.customer_list") },
          ]}
          action={
            <Button
              variant="contained"
              startIcon={<Iconify icon="eva:plus-fill" />}
              component={RouterLink}
              to={PATH_DASHBOARD.customer.add}
            >
              {t("p_customer.new_customer")}
            </Button>
          }
        />

        <Card>
          <CustomerTableToolbar
            filterName={filterName}
            onFilterName={handleFilterName}
            handleRefresh={handleRefresh}
            isLoading={isLoading}
            placeholder={t("params.search", {
              obj: t("p_customer.customer"),
            })}
            loadText={t("params.load")}
            title={t("params.filter_list")}
          />

          <Scrollbar>
            <TableContainer sx={{ minWidth: 800 }}>
              {selected.length > 0 && (
                <TableSelectedActions
                  dense={dense}
                  numSelected={selected.length}
                  rowCount={tableData.length}
                  selectedText={t("params.selected")}
                  onSelectAllRows={(checked) =>
                    onSelectAllRows(
                      checked,
                      tableData.map((row) => row.id)
                    )
                  }
                  actions={
                    <Tooltip title={t("params.delete")}>
                      <IconButton
                        color="primary"
                        onClick={() => {
                          const confirmation = window.confirm(
                            t("params.deletes_alert")
                          );

                          if (confirmation) {
                            handleDeleteRows(selected);
                          }
                        }}
                      >
                        <Iconify icon={"eva:trash-2-outline"} />
                      </IconButton>
                    </Tooltip>
                  }
                />
              )}

              <Table size={dense ? "small" : "medium"}>
                <TableHeadCustom
                  order={order}
                  orderBy={orderBy}
                  headLabel={TABLE_HEAD}
                  rowCount={tableData.length}
                  numSelected={selected.length}
                  onSort={onSort}
                  onSelectAllRows={(checked) =>
                    onSelectAllRows(
                      checked,
                      tableData.map((row) => row.id)
                    )
                  }
                />

                <TableBody>
                  {(isLoading ? [...Array(rowsPerPage)] : dataFiltered).map(
                    (row, index) =>
                      row ? (
                        <CustomerTableRow
                          key={index}
                          row={row}
                          selected={selected.includes(row.id)}
                          onSelectRow={() => onSelectRow(row.id)}
                          onDeleteRow={() => {
                            const confirmation = window.confirm(
                              t("params.delete_alert")
                            );

                            if (confirmation) {
                              handleDeleteRow(row.id);
                            }
                          }}
                          onEditRow={() => handleEditRow(row.id)}
                          deleteText={t("params.delete")}
                          editText={t("params.edit")}
                        />
                      ) : (
                        !isNotFound && (
                          <TableSkeleton
                            key={index}
                            sx={{ height: denseHeight }}
                          />
                        )
                      )
                  )}

                  <TableEmptyRows
                    height={denseHeight}
                    emptyRows={emptyRows(
                      page,
                      tableData.length,
                      tableData.length + customers.count
                    )}
                  />

                  <TableNoData
                    isNotFound={isNotFound}
                    title={t("params.no_data")}
                  />
                </TableBody>
              </Table>
            </TableContainer>
          </Scrollbar>

          <Box sx={{ position: "relative" }}>
            <TablePagination
              rowsPerPageOptions={[
                5,
                25,
                50,
                100,
                1000, 5000,
              ]}
              component="div"
              count={customers.count}
              rowsPerPage={rowsPerPage}
              page={page}
              onPageChange={onChangePage}
              onRowsPerPageChange={onChangeRowsPerPage}
              labelRowsPerPage={t("params.row_per_page")}
            />

            <FormControlLabel
              control={<Switch checked={dense} onChange={onChangeDense} />}
              label={t("params.dense")}
              sx={{ px: 3, py: 1.5, top: 0, position: { md: "absolute" } }}
            />
          </Box>
        </Card>
      </Container>
    </Page>
  );
}

// ----------------------------------------------------------------------

function applySortFilter({ tableData, comparator, filterName }) {
  const stabilizedThis = tableData.map((el, index) => [el, index]);

  stabilizedThis.sort((a, b) => {
    const order = comparator(a[0], b[0]);
    if (order !== 0) return order;
    return a[1] - b[1];
  });

  tableData = stabilizedThis.map((el) => el[0]);

  if (filterName) {
    tableData = tableData.filter(
      (item) =>
        item.from_currency_name
          .toLowerCase()
          .includes(filterName.toLowerCase()) ||
        item.to_currency_name.toLowerCase().includes(filterName.toLowerCase())
    );
  }

  return tableData;
}
